.page-faq {
  width: 100%;
  padding: 0 10%;
  margin: 40px 0;
  margin-bottom: 100px;
  margin-top: 180px;

  .faq-item {
    margin-bottom: 50px;

    h1 {
      margin: 0;
      font-family: "Lato-Light";
      font-style: normal;
      font-weight: 200;
      color: #ed1c2e;
      font-weight: 600;
      background-color: #fff;
      font-size: 24px;
      padding-bottom: 5px;
      border-bottom: 1px solid #ccc;
    }

    p {
      color: #666;
      font-size: 16px;
      line-height: 20px;
      text-align: justify;
    }
  }
}

@media screen and (max-width: 480px) {

  .page-faq {
    .faq-item {
  
      h1 {
        font-size: 18px;
      }
  
      p {
        font-size: 14px;
      }
    }
  }

}

@media screen and (max-width: 991px) {
  .page-faq {
    margin-top: 140px;
  }
}


